/* >>>>>> TEXT <<<<<< */

.area-title {
    text-align: left;
}
.order-recap-line {
    text-align: center;
}

.orders-details {
    color: rgb(165, 165, 165);
}

/* >>>>>> CONTAINERS & BUTTONS <<<<<< */

.orders-container {
    background-color: white;
    margin: 2vw;
    padding: 10vw;
    border-radius: 2em;
}




.area-bar {
    background-color: white;
    margin: 0;
    padding: 0;
    border: 0;
}

.btn-filter {
    text-align: center;
    margin: 1vw;
    padding: 2vw;
}

.flex {
    display: flex;
}

.item {
    padding: 1.3vw;
}

/* >>>>>> IMAGES & ICONS <<<<<< */
.orders-logo {
    width: 8vw;
    height: auto;
}

i.fas.fa-filter {
    color: black;
    width: 20px;
    height : auto;
}

/* >>>>>> TIMELINE <<<<<< */

.line {
    width: 90vw;
    height: 1vh;
    background-color: #DAABB0;
}

.bullet-points {
    width: 1.55vw;
    height: 3vh;
    border-radius: 50%;
    background-color: #DAABB0;
}

.label-date {
  color: #8a8a8a;
  font-size: 0.75em;
  font-family: "Open Sans";
  margin-top: 15px;
}

.input-date {
  border: none;
  font-family: "Open Sans";
  color: grey;
  border-bottom: 1px solid grey;
  padding: 0;
  margin-right: 2rem;
}

.received-card {
  /* position: "absolute";
    top: 50%;
    left: 50%;
    z-index: 100; */
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.received-item {
  margin-bottom: 2em;
}

.upload-icon {
  padding: 0.2em 0.7em 0.2em 0.7em;
}

.received-pic {
  width: 18em;
  height: auto;
  border-radius: 4em;
}

.shipped-card {
  /* position: "absolute";
  top: 50%;
  left: 50%;
  z-index: 100; */
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.shipped-item {
  margin-bottom: 2em;
}

.upload-icon {
  padding: 0.2em 0.7em 0.2em 0.7em;
}

.ship-pic {
  width: 18em;
  height: auto;
  border-radius: 4em;
}

