/* Basic set up for Material UI - roboto font mandatory and Material icons necessary if we want to use them */
/* @import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap"); */

:root {
  --dark-blue: #252a36;
  --light-pink: #eedbdd;
  --medium-pink: #daabb0;
  --burgundy: #64323e;
  --gold: #b69e60;
  --background: #f0f0f8;
  --light-background: #f4eeed;
  --dark-text: #252a37;
  --light-text: #626262;
  --white-text: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f0f0f8;
  background-color: var(--background);
}

html {
  font-family: "Open sans";
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

span {
  font-weight: bold;
  font-size: 1.2em;
}

#navbar {
  margin: 10px;
}
.auth-container {
  margin-top: 20px;
}

.current-users {
  margin: 5px 0px;
}

.button-box {
  margin-top: 10px;
}

.card {
  margin: 10px 0px;
}

.catBrandContainer {
  margin: 10px;
}

header .MuiGrid-container {
  width: unset !important; /* !important overides the property with the same class */
}

header .MuiButton-root {
  min-width: 12px !important;
}

.MuiButtonBase-root {
  margin: 10px 0px !important;
}

@media (min-width: 900px) {
  .dashboard-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 6fr;
    margin: 10px;
  }
  .dashboard-fullrow {
    grid-column: 1/3;
    grid-row: 1/2;
  }
  .dashboard-left {
    display: flex;
    flex-direction: column;
    grid-column: 1/2;
    grid-row: 2/3;
    padding-right: 10px;
  }
  .dashboard-right {
    grid-column: 2/3;
    grid-row: 2/3;
    padding-left: 10px;
  }
  .dashboard-scrollbox {
    margin-top: 10px;
    height: 40vh;
    overflow-y: scroll;
  }
}

@media (max-width: 899px) {
  .dashboard-container {
    margin: 10px;
  }
  .dashboard-scrollbox {
    margin-top: 10px;
    height: 40vh;
    overflow-y: scroll;
  }
}

